// mixins.js
import Vue from 'vue';
import Component from 'vue-class-component';
import {MessageBox, Message} from 'element-ui';


@Component
export class TagHelper extends Vue {
  createTag() {
    // const name = window.prompt('请输入标签名');
    // if (!name) { return window.alert('标签名不能为空');}
    MessageBox.prompt('请输入标签名', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
    })
      .then((res) => {
        console.log(res);
        // @ts-ignore
        if (!res.value) {
          return Message({
            showClose: true,
            type: 'error',
            message: '标签名不能为空',
          });
        }
        // @ts-ignore
        const str = res.value;

        function getStrLen(str:string) {
          const json = {len: 0};
          //判断标签名是不是汉字
          const re = /[\u4e00-\u9fa5]/;
          for (let i = 0; i < str.length; i++) {
            if (re.test(str.charAt(i))) {
              //汉字的长度为 2 ，字母的长度为 1
              json['len']++;
            }
          }
          return json['len'] + str.length;
        }

        if (getStrLen(str) > 8) return Message.error('标签名过长，请重新输入');

        // @ts-ignore
        const names = this.$store.state.tagList.map(item => item.name);
        // @ts-ignore
        if (names.indexOf(res.value) >= 0) {
          return Message({
            showClose: true,
            type: 'error',
            message: '标签名重复,请重新输入'
          });
        }
        // @ts-ignore
        this.$store.commit('createTag', res.value);
        Message({
          showClose: true,
          type: 'success',
          message: '成功添加标签'
        });
      })
      .catch(() => {
        this.$message({
          showClose: true,
          type: 'info',
          message: '取消输入'
        });
      });
  }
}
